.slider-wrapper {
  flex-direction: column;
  gap: 6px;
  width: 100%;
  display: flex;
}

.slider-row {
  align-items: center;
  gap: 8px;
  width: 100%;
  display: flex;
}

.slider-track-container {
  flex-grow: 1;
}

.slider-thumb-wrapper {
  align-items: flex-end;
  width: 100%;
  height: 40px;
  display: flex;
  position: relative;
}

.slider-value {
  color: #000;
  white-space: nowrap;
  pointer-events: none;
  z-index: 10;
  background: #f0f0ff;
  border-radius: 4px;
  padding: 4px 8px;
  font-size: 14px;
  font-weight: bold;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateX(-50%);
  box-shadow: 0 1px 2px #00000026;
}

.slider-min, .slider-max {
  color: #666;
  text-align: center;
  width: 24px;
  font-size: 12px;
}

input[type="range"] {
  appearance: none;
  background: linear-gradient(to right, #007bff 0% 0%, #ddd 0% 100%);
  border-radius: 3px;
  outline: none;
  width: 100%;
  height: 6px;
  transition: background .3s;
}

input[type="range"]::-webkit-slider-thumb {
  appearance: none;
  cursor: pointer;
  background: #007bff;
  border-radius: 50%;
  width: 16px;
  height: 16px;
  margin-top: -5px;
  box-shadow: 0 2px 4px #0003;
}

input[type="range"]::-moz-range-thumb {
  cursor: pointer;
  background: #007bff;
  border-radius: 50%;
  width: 16px;
  height: 16px;
  box-shadow: 0 2px 4px #0003;
}
